<template>
	<div class="list-wrapper">
		<p>下面呈现的按钮表明当前登录用户拥有该按钮权限:</p>
		<Button type="primary" v-has="['get,/medications']">查询</Button>
		<p style="margin-top: 20px;">允许请求:</p>
		<Button style="margin-bottom: 10px;" type="primary" @click="rightRequest">允许请求</Button>
		<Table v-if="allowdata.length" :columns="columns1" :data="allowdata"></Table>
		<p style="margin-top: 20px;">尝试发起越权请求：</p>
		<Button type="primary" @click="noallowRequest">越权请求</Button>
	</div>
</template>

<script>
	import AppModel from '@/model/app'
	import Bus from '../bus'
	const appInstance = new AppModel()

	export default {
		data () {
			return {
				allowdata: [],
				columns1: [
                    {
                        title: '名字',
                        key: 'name'
                    },
                    {
                        title: '标准',
                        key: 'license'
                    },
                    {
                        title: '包装',
                        key: 'standard'
                    }
                ]
			}
		},
		methods: {
			async noallowRequest() {
				const res = await appInstance.noallowRequest()
			},
			async rightRequest() {
				const res = await appInstance.allowRequest()
				this.allowdata = res.data.content.content
			}
		}
	}
</script>

<style>
	.list-wrapper{
		padding: 20px 0;
	}
</style>


